const magicGrid = {
    gridSelector: '.reviews-container',
    init: function () {
        let magicGrid = new MagicGrid({
            container: magicGrid.gridSelector, // Required. Can be a class, id, or an HTMLElement.
            items: 8, // For a grid with 20 items. Required for dynamic content.
            animate: true, // Optional.
        });
        magicGrid.listen();
    },
}
module.exports = magicGrid;